'use client';

import React, { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { useAppContext } from '../context/AppContext';
import { signIn } from 'next-auth/react';
import '../App.css'; // Import the main CSS file

interface AuthFormProps {
  onClose?: () => void;
}

const AuthForm: React.FC<AuthFormProps> = ({ onClose: _onClose = () => {} }) => {
  const { error, loading, setError, login, register } = useAppContext();
  const router = useRouter();
  const [redirect, setRedirect] = useState('/');
  const googleClientId = process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID || '';

  const navigate = (path: string) => {
    router.push(path);
  };

  const [isLogin, setIsLogin] = useState(true);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirmPassword: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    // Clear error when user starts typing
    if (error.auth) setError('auth', '');
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      if (isLogin) {
        // Login validation
        if (!formData.email || !formData.password) {
          throw new Error('Please fill in all fields');
        }

        await login({
          email: formData.email,
          password: formData.password
        });

        // Close modal and navigate to redirect URL or home page after login
        const targetPath = redirect && redirect !== '/' ? decodeURIComponent(redirect) : '/';
        if (targetPath === '/') {
          // If redirecting to home, just close the modal
          _onClose();
        } else {
          // Otherwise navigate to the redirect page
          navigate(targetPath);
        }
      } else {
        // Registration validation
        if (!formData.name || !formData.email || !formData.password) {
          throw new Error('Please fill in all fields');
        }

        if (formData.password !== formData.confirmPassword) {
          throw new Error('Passwords do not match');
        }

        // Password strength validation
        if (formData.password.length < 6) {
          throw new Error('Password must be at least 6 characters long');
        }

        if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/.test(formData.password)) {
          throw new Error('Password must contain at least one lowercase letter, one uppercase letter, and one digit');
        }

        await register({
          name: formData.name,
          email: formData.email,
          password: formData.password
        });

        // Close modal and navigate to redirect URL or home page after registration
        const targetPath = redirect && redirect !== '/' ? decodeURIComponent(redirect) : '/';
        if (targetPath === '/') {
          // If redirecting to home, just close the modal
          _onClose();
        } else {
          // Otherwise navigate to the redirect page
          navigate(targetPath);
        }
      }
    } catch (err) {
      console.error('Auth error:', err);
      // Error is handled by the context
    }
  };

  // Google Sign In handler
  const handleGoogleSuccess = async () => {
    try {
      // Use NextAuth to sign in with Google - don't redirect immediately
      // Instead, use callback to handle the response
      const result = await signIn('google', {
        redirect: false, // Don't redirect, use callback to handle response
      });
      
      if (result?.error) {
        console.error('Google sign-in error:', result.error);
        setError('auth', result.error || 'Google authentication failed. Please try again.');
      } else if (result?.ok) {
        // Google sign-in was successful, close modal
        // The session will be handled by NextAuth and sync to AppContext via useSyncNextAuthSession
        _onClose();
      }
    } catch (error) {
      console.error('Google login error:', error);
      setError('auth', 'Google authentication failed. Please try again.');
    }
  };

  // Read redirect param from URL on client side
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const params = new URLSearchParams(window.location.search);
      setRedirect(params.get('redirect') || '/');
    }
  }, []);

  const handleGoogleError = () => {
    console.error('Google Login Failed');
    // Provide a more user-friendly error message
    setError('auth', 'Google authentication failed. This might be due to a configuration issue. Please try regular login or contact support.');
  };

  return (
    <div className="auth-page">
      <div className="auth-container-elegant">
        <button onClick={_onClose} className="close-btn">&times;</button>
        <div className="auth-header-elegant">
          <div className="auth-title">
            <h2 className="auth-main-title">{isLogin ? 'Welcome Back' : 'Create Account'}</h2>
            <p className="auth-subtitle">{isLogin ? 'Sign in to continue your journey' : 'Join our community today'}</p>
          </div>
        </div>

        {error.auth && (
          <div className="auth-error-message">
            <div className="error-icon">⚠️</div>
            <span>{error.auth}</span>
          </div>
        )}

        <form onSubmit={handleSubmit} className="auth-form-elegant">
          {!isLogin && (
            <div className="form-group-elegant">
              <label htmlFor="name" className="form-label">Full Name</label>
              <input
                type="text"
                id="name"
                name="name"
                placeholder="Enter your full name"
                value={formData.name}
                onChange={handleChange}
                required={!isLogin}
                autoComplete="name"
                className="form-input"
              />
            </div>
          )}

          <div className="form-group-elegant">
            <label htmlFor="email" className="form-label">Email Address</label>
            <input
              type="email"
              id="email"
              name="email"
              placeholder="Enter your email"
              value={formData.email}
              onChange={handleChange}
              required
              autoComplete="email"
              className="form-input"
            />
          </div>

          <div className="form-group-elegant">
            <label htmlFor="password" className="form-label">Password</label>
            <input
              type="password"
              id="password"
              name="password"
              placeholder="Enter your password"
              value={formData.password}
              onChange={handleChange}
              required
              autoComplete="current-password"
              className="form-input"
            />
            {!isLogin && (
              <div className="password-requirements">
                <p className="requirements-text">Password must contain:</p>
                <ul className="requirements-list">
                  <li className={formData.password.length >= 6 ? 'valid' : 'invalid'}>At least 6 characters</li>
                  <li className={/(?=.*[a-z])/.test(formData.password) ? 'valid' : 'invalid'}>One lowercase letter</li>
                  <li className={/(?=.*[A-Z])/.test(formData.password) ? 'valid' : 'invalid'}>One uppercase letter</li>
                  <li className={/(?=.*\d)/.test(formData.password) ? 'valid' : 'invalid'}>One number</li>
                </ul>
              </div>
            )}
          </div>

          {!isLogin && (
            <div className="form-group-elegant">
              <label htmlFor="confirmPassword" className="form-label">Confirm Password</label>
              <input
                type="password"
                id="confirmPassword"
                name="confirmPassword"
                placeholder="Confirm your password"
                value={formData.confirmPassword}
                onChange={handleChange}
                required={!isLogin}
                autoComplete="new-password"
                className="form-input"
              />
            </div>
          )}

          <button
            type="submit"
            className={`auth-submit-btn ${isLogin ? 'login-btn' : 'register-btn'}`}
            disabled={loading.auth}
          >
            {loading.auth ? (
              <span className="loading-spinner">Loading...</span>
            ) : (
              <span>{isLogin ? 'Sign In' : 'Create Account'}</span>
            )}
          </button>
        </form>

        <div className="auth-separator">
          <div className="separator-line"></div>
          <span className="separator-text">or</span>
          <div className="separator-line"></div>
        </div>

        {/* Google Sign In Button */}
        <div className="google-auth-section-elegant">
          <button
            onClick={handleGoogleSuccess}
            className="google-signin-btn-elegant"
          >
            <svg width="20" height="20" viewBox="0 0 24 24" className="google-icon">
              <path 
                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" 
                fill="#4285F4"
              />
              <path 
                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" 
                fill="#34A853"
              />
              <path 
                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" 
                fill="#FBBC05"
              />
              <path 
                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" 
                fill="#EA4335"
              />
            </svg>
            <span>Continue with Google</span>
          </button>
        </div>

        <div className="auth-toggle-elegant">
          <p>
            {isLogin ? "Don't have an account? " : "Already have an account? "}
            <button 
              className="toggle-link"
              onClick={() => {
                setIsLogin(!isLogin);
                setError('auth', '');
              }}
            >
              {isLogin ? 'Sign Up' : 'Sign In'}
            </button>
          </p>
        </div>
      </div>
    </div>
  );
};

export default AuthForm;